	module.exports = {"data":{"site":{"siteMetadata":{"title":"Toshio Blog","author":"Ta Toshio"}},"markdownRemark":{"id":"/Volumes/Transcend/Workspace/local/ta-toshio-blog/src/pages/2018-12-11-install-docker-in-aws-ami-2/index.md absPath of file >>> MarkdownRemark","html":"<p>Amazon Linux 2 AMI (HVM)にDockerのインストール</p>\n<p>こちらの記事を参考に作業しました。\n<a href=\"https://qiita.com/reflet/items/3b818fbfb14ba5c7ef47\">https://qiita.com/reflet/items/3b818fbfb14ba5c7ef47</a></p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-text\"><code class=\"language-text\"># パッケージ更新\n$ sudo yum update -y\n\n# インストールできるパッケージを確認\n$ amazon-linux-extras list\n20  docker=latest            enabled      \\\n       [ =17.12.1  =18.03.1  =18.06.1 ]\n\n# インストール（ここでは=18.06.1を指定）\n$ sudo amazon-linux-extras install -y docker=18.06.1\n\n# 起動\n$ sudo service docker start\nRedirecting to /bin/systemctl start docker.service\n\n# 自動起動\n$ sudo systemctl enable docker\n\n# 確認\n$ sudo service docker status\nRedirecting to /bin/systemctl status docker.service\n● docker.service - Docker Application Container Engine\n   Loaded: loaded (/usr/lib/systemd/system/docker.service; disabled; vendor preset: disabled)\n   Active: active (running)\n   Docs: https://docs.docker.com\nProcess: 11987 ExecStartPre=/usr/libexec/docker/docker-setup-runtimes.sh (code=exited, status=0/SUCCESS)\nProcess: 11955 ExecStartPre=/bin/mkdir -p /run/docker (code=exited, status=0/SUCCESS)\nMain PID: 11999 (dockerd)\n  Tasks: 18\n Memory: 36.2M\n CGroup: /system.slice/docker.service\n         ├─11999 /usr/bin/dockerd --default-ulimit nofile=1024:4096\n         └─12010 docker-containerd --config /var/run/docker/containerd/containerd.toml\n\n# バージョン確認\n$ docker -v\nDocker version 18.06.1-ce, build e68fc7a215d7133c34aa18e3b72b4a21fd0c6136\n\n# dockerグループに参加する\n$ sudo usermod -a -G docker ec2-user\n$ cat /etc/group | grep docker\ndocker:x:993:ec2-user\n\n$ id\nuid=1000(ec2-user) gid=1000(ec2-user) groups=1000(ec2-user),4(adm),10(wheel),190(systemd-journal)\n\n# 設定を反映するため、sshを再接続する\n$ exit\n\n$ id\nuid=1000(ec2-user) gid=1000(ec2-user) groups=1000(ec2-user),4(adm),10(wheel),190(systemd-journal),993(docker)\n\n# Hello Worldを実行してみる\n$ docker run hello-world\nUnable to find image &#39;hello-world:latest&#39; locally\nlatest: Pulling from library/hello-world\n1b930d010525: Pull complete\nDigest: sha256:2557e3c07ed1e38f26e389462d03ed943586f744621577a99efb77324b0fe535\nStatus: Downloaded newer image for hello-world:latest\n\nHello from Docker!\n\n# 不要なコンテナ一括削除\n$ docker rm `docker ps -f &quot;status=exited&quot; -q`</code></pre>\n      </div>\n<hr>\n<p><em>他参考サイト</em> <br />\n<a href=\"https://qiita.com/y-do/items/e127211b32296d65803a\">https://qiita.com/y-do/items/e127211b32296d65803a</a></p>\n<p>amazon-linux-extrasについて<br />\n<a href=\"https://dev.classmethod.jp/cloud/aws/how-to-work-with-amazon-linux2-amazon-linux-extras/\">https://dev.classmethod.jp/cloud/aws/how-to-work-with-amazon-linux2-amazon-linux-extras/</a></p>","frontmatter":{"title":"Install Docker In Aws Ami 2","date":"December 11, 2018"}}},"pathContext":{"slug":"/2018-12-11-install-docker-in-aws-ami-2/","previous":{"fields":{"slug":"/2018-11-10-aws-ami-2-install-mysql-57/"},"frontmatter":{"title":"Aws Ami 2 Install Mysql 57"}},"next":{"fields":{"slug":"/2019-01-08-create-vpc-public-subnet-and-ec-2/"},"frontmatter":{"title":"Create Vpc Public Subnet And Ec 2"}}}}

